body {
    font-family: sans-serif;
    background-color: #fbfbfb;
  }
  
  .wrapper {
    width: 500px;
    margin: 10px auto;
    margin-top: 80px;
    font-size: 14px;
    background-color: #fff;
    border: 1px solid #f1f1f1;
    padding: 20px;
  }
  

  /* 登陆框样式 */
  .login-input {
    background-color: #fff;
    border: 1px solid #f1f1f1;
    padding: 20px;
    margin-bottom: 10px;
  }
  
  .login-field {
    margin-bottom: 15px;
    display: -ms-flexbox;
    display: flex;
  }
  
  .login-field .login-field-name {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-preferred-size: 100px;
        flex-basis: 100px;
    font-size: 14px;
  }
  
  .login-field .login-field-input {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
  }
  
  .login-field-input input,
  .login-field-input textarea {
    border: 1px solid #e6e6e6;
    border-radius: 3px;
    padding: 5px;
    outline: none;
    font-size: 14px;
    resize: none;
    -ms-flex: 1 1;
        flex: 1 1;
  }
  
  .login-field-input textarea {
    height: 100px;
  }

  .signin-field-title {
    font: bold 30px "Trebuchet MS", Verdana, Arial, Helvetica, sans-serif; 
    text-align: center;
    padding-top:15px;
    padding-bottom:15px;
    color: #2e89ad;
    padding: 0 180px;
  }

  .login-field-button {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    padding-bottom: 5px;
  }
  
  .login-field-button button {
    padding: 5px 10px;
    width: 80px;
    border: none;
    border-radius: 3px;
    background-color: #00a3cf;
    color: #fff;
    outline: none;
    cursor: pointer;
  }
  
  .login-field-button button:active {
    background: #13c1f1;
  }
  
  /* 登陆列表样式 */
  .login-list {
    background-color: #fff;
    border: 1px solid #f1f1f1;
    padding: 20px;
  }
  
  /* 登陆组件样式 */
  .login {
    display: -ms-flexbox;
    display: flex;
    border-bottom: 1px solid #f1f1f1;
    margin-bottom: 10px;
    padding-bottom: 10px;
    min-height: 50px;
  }
  
  .login .login-user {
    -ms-flex-negative: 0;
        flex-shrink: 0;
  }
  
  .login span {
    color: #00a3cf;
    font-style: italic;
  }
  
  .login p {
    margin: 0;
    /*text-indent: 2em;*/
  }
  


